'use client';

import { useState, type FormEvent, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/context/AuthContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { Heart } from '@/components/icons';
import { FirebaseError } from 'firebase/app';

type AuthMode = 'initial' | 'login' | 'signup';
type SignupStep = 'welcome' | 'name' | 'memorable-word' | 'credentials';

interface FormData {
  name: string;
  memorableCodeWord: string;
  email: string;
  password: string;
  confirmPassword: string;
}

interface FieldErrors {
  name?: string;
  memorableCodeWord?: string;
  email?: string;
  password?: string;
  confirmPassword?: string;
}

export default function LoginPage() {
  const [authMode, setAuthMode] = useState<AuthMode>('initial');
  const [signupStep, setSignupStep] = useState<SignupStep>('welcome');
  const [formData, setFormData] = useState<FormData>({
    name: '',
    memorableCodeWord: '',
    email: '',
    password: '',
    confirmPassword: '',
  });
  const [fieldErrors, setFieldErrors] = useState<FieldErrors>({});
  const [loginEmail, setLoginEmail] = useState('');
  const [loginPassword, setLoginPassword] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isClient, setIsClient] = useState(false);
  const { login, signup, user, loading } = useAuth();
  const router = useRouter();
  const { toast } = useToast();

  useEffect(() => {
    setIsClient(true);
  }, []);

  useEffect(() => {
    console.log('Login page - loading:', loading, 'user:', !!user);
    if (!loading && user) {
      console.log('User authenticated, redirecting to dashboard');
      router.push('/');
    }
  }, [user, loading, router]);

  // Validation functions
  const validateName = (): boolean => {
    const trimmed = formData.name.trim();
    if (!trimmed) {
      setFieldErrors({ ...fieldErrors, name: 'Please enter your name' });
      return false;
    }
    if (trimmed.length < 2) {
      setFieldErrors({ ...fieldErrors, name: 'Name must be at least 2 characters' });
      return false;
    }
    setFieldErrors({ ...fieldErrors, name: undefined });
    return true;
  };

  const validateMemorableWord = (): boolean => {
    const trimmed = formData.memorableCodeWord.trim();
    if (!trimmed) {
      setFieldErrors({ ...fieldErrors, memorableCodeWord: 'Please enter your memorable word' });
      return false;
    }
    if (trimmed.length < 3) {
      setFieldErrors({ ...fieldErrors, memorableCodeWord: 'Memorable word must be at least 3 characters' });
      return false;
    }
    setFieldErrors({ ...fieldErrors, memorableCodeWord: undefined });
    return true;
  };

  const validateCredentials = (): boolean => {
    let isValid = true;
    const errors: FieldErrors = {};

    if (!formData.email) {
      errors.email = 'Email is required';
      isValid = false;
    }
    if (!formData.password || formData.password.length < 6) {
      errors.password = 'Password must be at least 6 characters';
      isValid = false;
    }
    if (formData.password !== formData.confirmPassword) {
      errors.confirmPassword = 'Passwords do not match';
      isValid = false;
    }

    setFieldErrors(errors);
    return isValid;
  };

  // Login handler
  const handleLogin = async (e: FormEvent) => {
    e.preventDefault();
    if (!loginEmail || !loginPassword) {
      toast({
        title: 'Error',
        description: 'Please fill in all fields.',
        variant: 'destructive',
      });
      return;
    }

    setIsSubmitting(true);
    try {
      await login(loginEmail, loginPassword);
      // Login successful - redirect will happen via useEffect when user state updates
    } catch (error: any) {
      console.error(error);
      let description = 'Could not log you in. Please try again later.';
      if (error instanceof FirebaseError) {
        if (error.code === 'auth/invalid-credential' || error.code === 'auth/user-not-found' || error.code === 'auth/wrong-password') {
          description = 'Invalid credentials. Please check your email and password.';
        }
      }
      toast({
        title: 'Login Failed',
        description: description,
        variant: 'destructive',
      });
      setIsSubmitting(false);
    }
  };

  // Signup step navigation
  const handleContinueFromWelcome = () => {
    setSignupStep('name');
  };

  const handleContinueFromName = () => {
    if (validateName()) {
      setSignupStep('memorable-word');
    }
  };

  const handleContinueFromMemorableWord = () => {
    if (validateMemorableWord()) {
      setSignupStep('credentials');
    }
  };

  const handleSignupSubmit = async (e: FormEvent) => {
    e.preventDefault();
    if (!validateCredentials()) {
      return;
    }

    setIsSubmitting(true);
    try {
      await signup(formData.email, formData.password, formData.name, formData.memorableCodeWord);
      toast({
        title: 'Account Created',
        description: 'Welcome to Care Kit! Redirecting...',
      });
      // Redirect will happen via useEffect when user state updates
    } catch (error: any) {
      console.error(error);
      let description = 'Could not create your account. Please try again.';
      if (error instanceof FirebaseError) {
        if (error.code === 'auth/email-already-in-use') {
          description = 'This email is already in use.';
        } else if (error.code === 'auth/weak-password') {
          description = 'Password must be at least 6 characters.';
        }
      }
      toast({
        title: 'Sign Up Failed',
        description,
        variant: 'destructive',
      });
      setIsSubmitting(false);
    }
  };

  const handleBack = () => {
    if (authMode === 'login') {
      setAuthMode('initial');
      setLoginEmail('');
      setLoginPassword('');
    } else if (authMode === 'signup') {
      if (signupStep === 'welcome') {
        setAuthMode('initial');
        setSignupStep('welcome');
        setFormData({
          name: '',
          memorableCodeWord: '',
          email: '',
          password: '',
          confirmPassword: '',
        });
        setFieldErrors({});
      } else if (signupStep === 'name') {
        setSignupStep('welcome');
      } else if (signupStep === 'memorable-word') {
        setSignupStep('name');
      } else if (signupStep === 'credentials') {
        setSignupStep('memorable-word');
      }
    }
  };

  // Wait for client-side hydration
  if (!isClient) {
    return (
      <div className="flex h-full items-center justify-center bg-background">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-dashed rounded-full animate-spin border-primary-foreground mx-auto"></div>
          <p className="mt-4 text-muted-foreground">Loading...</p>
        </div>
      </div>
    );
  }

  // If user is authenticated, show loading while redirecting
  if (user) {
    return (
      <div className="flex h-full items-center justify-center bg-background">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-dashed rounded-full animate-spin border-primary-foreground mx-auto"></div>
          <p className="mt-4 text-muted-foreground">Redirecting...</p>
        </div>
      </div>
    );
  }

  // Render initial view
  const renderInitialView = () => (
    <Card className="w-full max-w-sm shadow-xl">
      <CardHeader className="text-center">
        <Heart className="mx-auto h-12 w-12 text-primary-foreground fill-accent mb-4" />
        <CardTitle className="font-headline text-2xl">Care Kit</CardTitle>
        <CardDescription>Begin your journey to well-being.</CardDescription>
      </CardHeader>
      <CardContent className="space-y-3">
        <Button
          onClick={() => setAuthMode('login')}
          className="w-full"
        >
          Sign In
        </Button>
        <Button
          onClick={() => setAuthMode('signup')}
          variant="outline"
          className="w-full"
        >
          Sign Up
        </Button>
      </CardContent>
    </Card>
  );

  // Render login form
  const renderLoginForm = () => (
    <Card className="w-full max-w-sm shadow-xl">
      <CardHeader className="text-center">
        <Heart className="mx-auto h-12 w-12 text-primary-foreground fill-accent mb-4" />
        <CardTitle className="font-headline text-2xl">Welcome Back</CardTitle>
        <CardDescription>Log in to continue your affirmation journey.</CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleLogin} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="email">Email</Label>
            <Input
              id="email"
              type="email"
              placeholder="you@example.com"
              value={loginEmail}
              onChange={(e) => setLoginEmail(e.target.value)}
              required
              disabled={isSubmitting}
            />
          </div>
          <div className="space-y-2">
            <Label htmlFor="password">Password</Label>
            <Input
              id="password"
              type="password"
              placeholder="••••••••"
              value={loginPassword}
              onChange={(e) => setLoginPassword(e.target.value)}
              required
              disabled={isSubmitting}
            />
          </div>
          <Button type="submit" className="w-full" disabled={isSubmitting}>
            {isSubmitting ? 'Logging In...' : 'Log In'}
          </Button>
        </form>

        <div className="mt-4">
          <Button
            type="button"
            variant="ghost"
            onClick={handleBack}
            className="w-full"
            disabled={isSubmitting}
          >
            ← Back
          </Button>
        </div>
      </CardContent>
    </Card>
  );

  // Render signup welcome step
  const renderWelcomeStep = () => (
    <Card className="w-full max-w-sm shadow-xl">
      <CardHeader className="text-center">
        <Heart className="mx-auto h-12 w-12 text-primary-foreground fill-accent mb-4" />
        <CardTitle className="font-headline text-2xl">Hello, welcome to Care Kit</CardTitle>
        <CardDescription>Let's get you set up. This will only take a moment.</CardDescription>
      </CardHeader>
      <CardContent className="space-y-3">
        <Button
          onClick={handleContinueFromWelcome}
          className="w-full"
        >
          Continue
        </Button>
        <Button
          type="button"
          variant="ghost"
          onClick={handleBack}
          className="w-full"
        >
          ← Back
        </Button>
      </CardContent>
    </Card>
  );

  // Render name step
  const renderNameStep = () => (
    <Card className="w-full max-w-sm shadow-xl">
      <CardHeader className="text-center">
        <Heart className="mx-auto h-12 w-12 text-primary-foreground fill-accent mb-4" />
        <CardTitle className="font-headline text-2xl">What's your name?</CardTitle>
        <CardDescription>We'd love to know what to call you.</CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="name">Name</Label>
          <Input
            id="name"
            type="text"
            placeholder="Your name"
            value={formData.name}
            onChange={(e) => {
              setFormData({ ...formData, name: e.target.value });
              setFieldErrors({ ...fieldErrors, name: undefined });
            }}
            onBlur={validateName}
            autoFocus
          />
          {fieldErrors.name && (
            <p className="text-sm text-destructive">{fieldErrors.name}</p>
          )}
        </div>
        <Button
          onClick={handleContinueFromName}
          className="w-full"
          disabled={!formData.name.trim()}
        >
          Continue
        </Button>
        <Button
          type="button"
          variant="ghost"
          onClick={handleBack}
          className="w-full"
        >
          ← Back
        </Button>
      </CardContent>
    </Card>
  );

  // Render memorable word step
  const renderMemorableWordStep = () => (
    <Card className="w-full max-w-sm shadow-xl">
      <CardHeader className="text-center">
        <Heart className="mx-auto h-12 w-12 text-primary-foreground fill-accent mb-4" />
        <CardTitle className="font-headline text-2xl">What's your memorable word from the survey?</CardTitle>
        <CardDescription>This helps us connect your data securely.</CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="memorableWord">Memorable Word</Label>
          <Input
            id="memorableWord"
            type="text"
            placeholder="Your memorable word"
            value={formData.memorableCodeWord}
            onChange={(e) => {
              setFormData({ ...formData, memorableCodeWord: e.target.value });
              setFieldErrors({ ...fieldErrors, memorableCodeWord: undefined });
            }}
            onBlur={validateMemorableWord}
            autoFocus
          />
          {fieldErrors.memorableCodeWord && (
            <p className="text-sm text-destructive">{fieldErrors.memorableCodeWord}</p>
          )}
        </div>
        <Button
          onClick={handleContinueFromMemorableWord}
          className="w-full"
          disabled={!formData.memorableCodeWord.trim()}
        >
          Continue
        </Button>
        <Button
          type="button"
          variant="ghost"
          onClick={handleBack}
          className="w-full"
        >
          ← Back
        </Button>
      </CardContent>
    </Card>
  );

  // Render credentials step
  const renderCredentialsStep = () => (
    <Card className="w-full max-w-sm shadow-xl">
      <CardHeader className="text-center">
        <Heart className="mx-auto h-12 w-12 text-primary-foreground fill-accent mb-4" />
        <CardTitle className="font-headline text-2xl">Create Your Account</CardTitle>
        <CardDescription>Just a few more details to get started.</CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSignupSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="signup-email">Email</Label>
            <Input
              id="signup-email"
              type="email"
              placeholder="you@example.com"
              value={formData.email}
              onChange={(e) => {
                setFormData({ ...formData, email: e.target.value });
                setFieldErrors({ ...fieldErrors, email: undefined });
              }}
              required
              disabled={isSubmitting}
              autoFocus
            />
            {fieldErrors.email && (
              <p className="text-sm text-destructive">{fieldErrors.email}</p>
            )}
          </div>
          <div className="space-y-2">
            <Label htmlFor="signup-password">Password</Label>
            <Input
              id="signup-password"
              type="password"
              placeholder="••••••••"
              value={formData.password}
              onChange={(e) => {
                setFormData({ ...formData, password: e.target.value });
                setFieldErrors({ ...fieldErrors, password: undefined });
              }}
              required
              disabled={isSubmitting}
            />
            {fieldErrors.password && (
              <p className="text-sm text-destructive">{fieldErrors.password}</p>
            )}
          </div>
          <div className="space-y-2">
            <Label htmlFor="confirm-password">Confirm Password</Label>
            <Input
              id="confirm-password"
              type="password"
              placeholder="••••••••"
              value={formData.confirmPassword}
              onChange={(e) => {
                setFormData({ ...formData, confirmPassword: e.target.value });
                setFieldErrors({ ...fieldErrors, confirmPassword: undefined });
              }}
              required
              disabled={isSubmitting}
            />
            {fieldErrors.confirmPassword && (
              <p className="text-sm text-destructive">{fieldErrors.confirmPassword}</p>
            )}
          </div>
          <Button type="submit" className="w-full" disabled={isSubmitting}>
            {isSubmitting ? 'Creating Account...' : 'Create Account'}
          </Button>
        </form>

        <div className="mt-4">
          <Button
            type="button"
            variant="ghost"
            onClick={handleBack}
            className="w-full"
            disabled={isSubmitting}
          >
            ← Back
          </Button>
        </div>
      </CardContent>
    </Card>
  );

  // Render signup flow based on current step
  const renderSignupFlow = () => {
    switch (signupStep) {
      case 'welcome':
        return renderWelcomeStep();
      case 'name':
        return renderNameStep();
      case 'memorable-word':
        return renderMemorableWordStep();
      case 'credentials':
        return renderCredentialsStep();
      default:
        return renderWelcomeStep();
    }
  };

  // Main render logic
  const renderContent = () => {
    switch (authMode) {
      case 'initial':
        return renderInitialView();
      case 'login':
        return renderLoginForm();
      case 'signup':
        return renderSignupFlow();
      default:
        return renderInitialView();
    }
  };

  return (
    <main className="flex min-h-full w-full items-center justify-center bg-background p-4">
      <div key={`${authMode}-${signupStep}`} className="animate-fade-in">
        {renderContent()}
      </div>
    </main>
  );
}
